.container {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.itemName {
  margin-left: 24px;
  font-size: 12px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 1.25px;
  text-transform: uppercase;
  text-decoration: none;
  color: #010101;
}
.itemName:hover,
.itemName:focus {
  color: #4b07e9e4;
}
.span {
  position: absolute;
  margin-left: 4px;
}
.spanOut {
  position: absolute;
  margin-right: 10px;
}
.btnText {
  margin-right: 3px;

  font-size: 12px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 1.25px;
  text-transform: uppercase;
  text-decoration: none;
  color: white;
}
.btn {
  padding: 2px 25px 2px 10px;
  margin-left: 40px;
  background-color: rgba(106, 20, 182, 0.673);
  border-radius: 10px;
}
.btn:hover,
.btn:focus {
  background-color: #4b07e9e4;
  color: white;
}
